import './App.css';
import { BrowserRouter as Router, Routes, Route } from "react-router-dom"
import Header from './components/header/Header';
import Home from './pages/home/home';
import MovieList from './components/movieList/movieList';
import Movie from './pages/movieDetail/movie';

function App() {
  return (
    <div className="App">
      <Router>
        <Header />
        <Routes>
          <Route index element={<Home />}></Route>
          <Route path="/movieopolis/movie/:id" element={<Movie />}></Route>
          <Route path="/movieopolis/movies/:type" element={<MovieList />}></Route>
          <Route path="/*" element={<h1>Oops ! Error 404 page not found</h1>}></Route>
        </Routes>
      </Router>
    </div>
  );
}

export default App;
